* {
    box-sizing: border-box;
    color:#293264;
}
body{
    background: url("./background.png") no-repeat;
    background-size:cover;

}

.container{
    width:100%;
    height:100vh;
    padding:20px;
    margin:0;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}
.start_container{
    text-align: center;
}
.title{
    color:#293264;
    font-size:31px;
    font-weight: 700;
    margin:0;
    font-family: Karla;
}
.des{
    color:#293264;
    font-size:16px;
    margin:22px 0;
}
.start_but{
    width: 193px;
    height: 52px;
    background: #4D5B9E;
    border-radius: 15px;
    color:#fff;
    border:none;
    font-size: 16px;
}
.q_c{
    border-bottom:  1px solid #DBDEF0;
    padding:10px;
}
.ans-container{
    display: flex
}
.ans{
    display: block;
    border: 1px solid #4D5B9E;
    border-radius: 8px;
    padding:4px 20px;
    margin-right:8px;
    min-width:50px;
    text-align: center;
    margin-top:0;
    background: rgba(255,255,255,0.5);
}
.ans-c{
    background: #D6DBF5;
    display: block;
    border: none;
    border-radius: 8px;
    padding:4px 20px;
    margin-right:8px;
    min-width:50px;
    text-align: center;
    margin-top:0;
}
.submit_but{
    width: 120px;
    height: 35px;
    background: #4D5B9E;
    border-radius: 15px;
    color:#fff;
    border:none;
    margin:20px;
}
.ans-green{
    background: #94D7A2;
    display: block;
    border: none;
    border-radius: 8px;
    padding:4px 20px;
    margin-right:8px;
    min-width:50px;
    text-align: center;
    margin-top:0;
    color:#293264;
}
.ans-pink{
    background: #F8BCBC;
    display: block;
    border: none;
    border-radius: 8px;
    padding:4px 20px;
    margin-right:8px;
    min-width:50px;
    text-align: center;
    margin-top:0;
}
.ans-grey{
    background:#fff;
    display: block;
    border: 1px solid #4D5B9E;
    border-radius: 8px;
    padding:4px 20px;
    margin-right:8px;
    min-width:50px;
    text-align: center;
    margin-top:0;
    color:#293264;
    opacity: 0.5;
}
.button_c{
    display: flex;
    justify-content: center;
    text-align: center;
    align-items: center;
}
.score{
    font-weight: 700;
    font-size: 15px;
    color: #293264;
}